import ReactDOM from "react-dom/client";
import { StrictMode } from "react";
import FieldTypeDocumentation from "../components/documentation/field-type-documentation";
import groceryCrudReducers from "../reducers";
import { configureStore } from "@reduxjs/toolkit";
import { Provider } from "react-redux";
import { configurationActions } from "../actions/configuration-actions";

const groceryCrudFieldTypeDocumentation = (myElement) => {
  // Transform below text into a react component
  //These are the available field types that you can choose when you use the function
  // `$crud->fieldType('field_name', 'field_type')` in Grocery CRUD.

  const groceryCrudStore = configureStore({
    reducer: groceryCrudReducers,
    devTools: true,
  });

  groceryCrudStore.dispatch({
    type: configurationActions.SET_DATE_FORMAT,
    dateFormat: "dd/MM/yyyy",
  });

  const root = ReactDOM.createRoot(myElement);

  root.render(
    <StrictMode>
      <Provider store={groceryCrudStore}>
        <FieldTypeDocumentation />
      </Provider>
    </StrictMode>
  );
};

if (window) {
  // Also make it available globally
  window.groceryCrudFieldTypeDocumentation = groceryCrudFieldTypeDocumentation;
}

export default groceryCrudFieldTypeDocumentation;
