import { useController } from "react-hook-form";
import PropTypes from "prop-types";
import { createUseStyles } from "react-jss";
import classNames from "classnames";
import darkModeSkin from "../../../style/common/dark-mode.skin";

const useStyles = createUseStyles(darkModeSkin);

function HtmlInput({
  control,
  name: originalName,
  value: originalValue,
  className: originalClassName,
  required,
  id,
  fieldOptions: { maxLength, hint_text } = {},
  domType,
  domStep,
  skin,
}) {
  const {
    field: { onChange, onBlur, name, value },
  } = useController({
    name: originalName,
    control,
    defaultValue: originalValue,
    shouldUnregister: true,
  });

  const classes = useStyles();
  const className = classNames(originalClassName, {
    [classes["dark-color-scheme"]]: skin === "dark",
  });

  return (
    <input
      className={className}
      id={id}
      name={name}
      onBlur={onBlur}
      onChange={onChange}
      placeholder={hint_text}
      title={hint_text}
      required={required}
      type={domType}
      value={value}
      step={domStep}
      maxLength={maxLength}
    />
  );
}

HtmlInput.propTypes = {
  className: PropTypes.string,
  control: PropTypes.object,
  domType: PropTypes.string,
  domStep: PropTypes.string,
  id: PropTypes.string,
  name: PropTypes.string,
  onBlur: PropTypes.func,
  onChange: PropTypes.func,
  required: PropTypes.bool,
  value: PropTypes.string,
};

export default HtmlInput;
